.header {
    margin-top: .5rem;
    padding-left: 10px;
}
  
.header ul li {
list-style: none;
margin: 0;
padding: .5rem 1rem;
border-radius: 3px;
}

.header ul li:hover {
background-color: #444;
}

.header a:hover {
text-decoration: none;
}

.header h3 a {
color: white;
}

.header h3 a:hover {
color: #00bc8c;
}
@keyframes lds-double-ring {
    0% {
      -webkit-transform: rotate(0);
      transform: rotate(0);
    }
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
}
  
@-webkit-keyframes lds-double-ring {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}
@keyframes lds-double-ring_reverse {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(-360deg);
        transform: rotate(-360deg);
    }
}
@-webkit-keyframes lds-double-ring_reverse {
    0% {
        -webkit-transform: rotate(0);
        transform: rotate(0);
    }
    100% {
        -webkit-transform: rotate(-360deg);
        transform: rotate(-360deg);
    }
}

.lds-css {
    margin: 0 auto;
}

.lds-double-ring {
    position: relative;
}
.lds-double-ring div {
    position: absolute;
    width: 160px;
    height: 160px;
    top: 20px;
    left: 20px;
    border-radius: 50%;
    border: 8px solid #000;
    border-color: #3d7ec0 transparent #3d7ec0 transparent;
    -webkit-animation: lds-double-ring 1.5s linear infinite;
    animation: lds-double-ring 1.5s linear infinite;
}
.lds-double-ring div:nth-child(2) {
    width: 140px;
    height: 140px;
    top: 30px;
    left: 30px;
    border-color: transparent #cc4545 transparent #cc4545;
    -webkit-animation: lds-double-ring_reverse 1.5s linear infinite;
    animation: lds-double-ring_reverse 1.5s linear infinite;
}
.lds-double-ring {
    width: 77px !important;
    height: 77px !important;
    -webkit-transform: translate(-38.5px, -38.5px) scale(0.385) translate(38.5px, 38.5px);
    transform: translate(-38.5px, -38.5px) scale(0.385) translate(38.5px, 38.5px);
}
.error-indicator {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #c78f22;
}
  
.error-indicator img {
margin-bottom: 1rem;
}

.error-indicator .boom {
font-size: 1.7rem;
}
.random-planet {
    display: flex;
    padding: 1rem;
}
  
.random-planet .planet-image {
width: 150px;
height: 150px;
border-radius: 10px;
margin-right: 1rem;
}

.random-planet .list-group {
margin: 1rem 0 0 1rem;
}

.random-planet .list-group-item {
padding: .25rem;
}

.random-planet .list-group-item .term {
margin-right: .5rem;
}
.item-list .list-group-item {
    cursor: pointer;
}
  
.item-list .list-group-item:hover {
background-color: #444;
}
.person-details {
    display: flex;
    margin-top: 1rem;
    flex-direction: row;
    padding: 1rem;
}
  
.person-details .person-image {
width: 30%;
height: 30%;
border-radius: 10px;
}

.person-details .list-group-item {
padding: .25rem;
}

.person-details .list-group-item .term{
margin-right: .5rem;
}



